body {
  background: #1e1e1e;
  color: #fff;
}

h1 {
  text-align: center;
  font-family: Urbanist;
  font-style: normal;
  font-weight: 900;
  font-size: clamp(18px, 2.5vw + 1rem, 29px);
  line-height: 1.1;
  letter-spacing: -1.45px;
}

p {
  text-align: center;
  font-family: Urbanist;
  font-size: clamp(48px, 8vw + 2rem, 123px);
  font-style: normal;
  font-weight: 900;
  line-height: 1.1;
  letter-spacing: -6.15px;
}
.main {
  display: flex;
  padding: 0vh 40px 0vh 40px;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  gap: 192px;
}
